/* custom color */

$colorSecondarySearch1: #3282f7;
$colorSecondarySearch2: #68d9a4;
$colorSecondarySearch3: #ed6454;

.header_searchbar-secondarySearchContainer1 {
  background-color: $colorSecondarySearch1;
  input {
    background-color: $colorSecondarySearch1;
    :-ms-input-placeholder {
      color: white;
    }
  }
}
.search_secondarySearchContainer-secondarySearch1::placeholder{
color: white;
}
.search_secondarySearchContainer-secondarySearch2::placeholder{
 color: white;
}
.search_secondarySearchContainer-secondarySearch3::placeholder{
color: white;
}

.container-input1:focus-within{
  img{
    transform: rotate(180deg)  ;
    transition-duration: 0.4s;

  }
}
.container-input2:focus-within{
  img{
    transform: rotate(180deg)  ;
    transition-duration: 0.4s;

  }
}
.container-input3:focus-within{
