.header_searchbar-mainSearchContainer {
  width: 95%;
  height: 69px;

  border-radius: 5px;
  display: flex;
  align-items: center;
}

.header_searchbar_mainSearch {
  width: 90%;
  height: 100%;
  border: none;
}
input:focus-visible {
  outline: none;
  border: none;
}
input {
  border: none;
}

.form-control:focus {
  background-color: inherit;
  border: none;
  box-shadow: none;
}
.container-secondarySearch1_suggestions{
